const viewAll = document.querySelector('.btn-view__all');
const cardsLength = document.querySelectorAll('.reviews__item').length;
let items = 3;

viewAll.addEventListener('click', () => {
  items += 3;
  const array = Array.from(document.querySelector('.reviews__list').children);
  const visItems = array.slice(0, items);

  visItems.forEach(el => el.classList.add('is-visible'));

  if (visItems.length === cardsLength) {
    viewAll.style.display = 'none';
  };
});
